'use client';

import { useState, useEffect } from 'react';
import { supabase } from '@/lib/supabaseClient';
import dynamic from 'next/dynamic';
import AuthModal from '@/components/AuthModal';
import { User } from '@supabase/supabase-js';
import { REASONS, REASON_TAGS, ReasonType } from '@/constants/reasons'; // ËøΩÂä†

const HazardMap = dynamic(() => import('@/components/HazardMap'), {
    loading: () => <div className="p-10 text-center">Âú∞Âõ≥„ÇíË™≠„ÅøËæº„Åø‰∏≠...</div>,
    ssr: false
});

const STORAGE_KEY = 'hazard-map-pos';

export default function Home() {
    const [posts, setPosts] = useState<any[]>([]);
    const [center, setCenter] = useState({ lat: 35.9251, lng: 139.4858 });
    const [zoom, setZoom] = useState(14);

    const [isModalOpen, setIsModalOpen] = useState(false);
    const [isAuthModalOpen, setIsAuthModalOpen] = useState(false);

    // „Éï„Ç©„Éº„É†Áî®State
    const [formReason, setFormReason] = useState<ReasonType>('Êöó„ÅÑ'); // ÂàùÊúüÂÄ§Â§âÊõ¥
    const [formTags, setFormTags] = useState<string[]>([]); // „Çø„Ç∞Áî®StateËøΩÂä†
    const [formTime, setFormTime] = useState('night');

    const [isSubmitting, setIsSubmitting] = useState(false);
    const [isLoadingGPS, setIsLoadingGPS] = useState(false);
    const [user, setUser] = useState<User | null>(null);

    // „É¶„Éº„Ç∂„ÉºÁõ£Ë¶ñ & Ëá™ÂãïÂåøÂêç„É≠„Ç∞„Ç§„É≥
    useEffect(() => {
        supabase.auth.getSession().then(({ data: { session } }) => {
            if (session?.user) {
                setUser(session.user);
            } else {
                signInAnonymously();
            }
        });

        const {
            data: { subscription }
        } = supabase.auth.onAuthStateChange((_event, session) => {
            setUser(session?.user ?? null);
        });

        return () => subscription.unsubscribe();
    }, []);

    const signInAnonymously = async () => {
        const { error } = await supabase.auth.signInAnonymously();
        if (error) console.error('Anonymous auth error:', error);
    };

    // ‰ΩçÁΩÆÊÉÖÂ†±„ÅÆÂæ©ÂÖÉ
    useEffect(() => {
        const savedPos = localStorage.getItem(STORAGE_KEY);
        if (savedPos) {
            try {
                const parsed = JSON.parse(savedPos);
                if (parsed.lat && parsed.lng && parsed.zoom) {
                    setCenter({ lat: parsed.lat, lng: parsed.lng });
                    setZoom(parsed.zoom);
                }
            } catch (e) {
                console.error(e);
            }
        }
    }, []);

    const fetchPosts = async () => {
        const { data, error } = await supabase.from('hazard_posts').select('*').order('created_at', { ascending: false });

        if (error) console.error('Error:', error);
        else setPosts(data || []);
    };

    useEffect(() => {
        fetchPosts();
    }, []);

    // „É°„Ç§„É≥„ÅÆÁêÜÁî±„ÅåÂ§â„Çè„Å£„Åü„Çâ„Çø„Ç∞ÈÅ∏Êäû„Çí„É™„Çª„ÉÉ„Éà
    useEffect(() => {
        setFormTags([]);
    }, [formReason]);

    const handleMapChange = (lat: number, lng: number, newZoom: number) => {
        setCenter({ lat, lng });
        setZoom(newZoom);
        localStorage.setItem(STORAGE_KEY, JSON.stringify({ lat, lng, zoom: newZoom }));
    };

    const handleCurrentLocation = () => {
        if (!navigator.geolocation) {
            alert('„Åä‰Ωø„ÅÑ„ÅÆ„Éñ„É©„Ç¶„Ç∂„Åß„ÅØÁèæÂú®Âú∞Ê©üËÉΩ„Åå‰Ωø„Åà„Åæ„Åõ„Çì');
            return;
        }
        setIsLoadingGPS(true);
        navigator.geolocation.getCurrentPosition(
            (position) => {
                const { latitude, longitude } = position.coords;
                setCenter({ lat: latitude, lng: longitude });
                setZoom(16);
                setIsLoadingGPS(false);
                localStorage.setItem(STORAGE_KEY, JSON.stringify({ lat: latitude, lng: longitude, zoom: 16 }));
            },
            (error) => {
                console.error(error);
                alert('ÁèæÂú®Âú∞„ÇíÂèñÂæó„Åß„Åç„Åæ„Åõ„Çì„Åß„Åó„Åü„ÄÇ');
                setIsLoadingGPS(false);
            }
        );
    };

    // „Çø„Ç∞„ÅÆÈÅ∏Êäû„ÉªËß£Èô§Âá¶ÁêÜ
    const toggleTag = (tag: string) => {
        setFormTags(
            (prev) =>
                prev.includes(tag)
                    ? prev.filter((t) => t !== tag) // Êó¢„Å´„ÅÇ„Çå„Å∞ÂâäÈô§
                    : [...prev, tag] // „Å™„Åë„Çå„Å∞ËøΩÂä†
        );
    };

    const handlePost = async (e: React.FormEvent) => {
        e.preventDefault();
        if (!user) {
            alert('Ë™çË®ºÊÉÖÂ†±„ÅÆÂèñÂæó‰∏≠„Åß„Åô„ÄÇÂ∞ë„ÄÖ„ÅäÂæÖ„Å°„Åè„Å†„Åï„ÅÑ„ÄÇ');
            return;
        }

        setIsSubmitting(true);
        const { error } = await supabase.from('hazard_posts').insert([
            {
                lat: center.lat,
                lng: center.lng,
                reason: formReason,
                tags: formTags, // ËøΩÂä†ÔºöÈÅ∏Êäû„Åï„Çå„Åü„Çø„Ç∞ÈÖçÂàó
                time_slot: formTime,
                user_id: user.id
            }
        ]);

        setIsSubmitting(false);

        if (error) {
            alert(`„Ç®„É©„Éº„ÅåÁô∫Áîü„Åó„Åæ„Åó„Åü: ${error.message}`);
        } else {
            alert('ÊäïÁ®ø„Åó„Åæ„Åó„ÅüÔºÅ');
            setIsModalOpen(false);
            setFormTags([]); // „Çø„Ç∞„ÇÇ„É™„Çª„ÉÉ„Éà
            fetchPosts();
        }
    };

    const handleLogout = async () => {
        await supabase.auth.signOut();
        window.location.reload();
    };

    return (
        <main style={{ width: '100%', height: '100dvh', display: 'flex', flexDirection: 'column', overflow: 'hidden' }}>
            <header
                style={{
                    padding: '10px 15px',
                    background: '#222',
                    color: '#fff',
                    zIndex: 10,
                    display: 'flex',
                    justifyContent: 'space-between',
                    alignItems: 'center',
                    flexShrink: 0
                }}
            >
                <h1 style={{ margin: 0, fontSize: '16px' }}>„Åø„Çì„Å™„ÅÆ„Éè„Ç∂„Éº„Éâ„Éû„ÉÉ„Éó</h1>

                <div>
                    {user && !user.is_anonymous ? (
                        <div style={{ display: 'flex', alignItems: 'center', gap: '10px' }}>
                            <span style={{ fontSize: '12px', color: '#ccc' }}>„É≠„Ç∞„Ç§„É≥‰∏≠</span>
                            <button
                                onClick={handleLogout}
                                style={{
                                    fontSize: '12px',
                                    padding: '4px 8px',
                                    background: '#555',
                                    color: '#fff',
                                    border: 'none',
                                    borderRadius: '4px',
                                    cursor: 'pointer'
                                }}
                            >
                                „É≠„Ç∞„Ç¢„Ç¶„Éà
                            </button>
                        </div>
                    ) : (
                        <button
                            onClick={() => setIsAuthModalOpen(true)}
                            style={{
                                fontSize: '12px',
                                padding: '6px 12px',
                                background: '#0070f3',
                                color: '#fff',
                                border: 'none',
                                borderRadius: '4px',
                                cursor: 'pointer',
                                fontWeight: 'bold'
                            }}
                        >
                            „É≠„Ç∞„Ç§„É≥ / ÁôªÈå≤
                        </button>
                    )}
                </div>
            </header>

            <div style={{ flex: 1, position: 'relative' }}>
                <HazardMap posts={posts} centerPos={center} zoomLevel={zoom} onMapChange={handleMapChange} />

                {!isModalOpen && (
                    <button
                        onClick={handleCurrentLocation}
                        disabled={isLoadingGPS}
                        style={{
                            position: 'absolute',
                            bottom: '110px',
                            right: '20px',
                            width: '50px',
                            height: '50px',
                            borderRadius: '50%',
                            background: 'white',
                            color: '#333',
                            fontSize: '24px',
                            border: 'none',
                            boxShadow: '0 4px 10px rgba(0,0,0,0.3)',
                            cursor: 'pointer',
                            zIndex: 1001,
                            display: 'flex',
                            alignItems: 'center',
                            justifyContent: 'center'
                        }}
                    >
                        {isLoadingGPS ? '...' : 'üìç'}
                    </button>
                )}

                {!isModalOpen && (
                    <button
                        onClick={() => setIsModalOpen(true)}
                        style={{
                            position: 'absolute',
                            bottom: '40px',
                            right: '20px',
                            width: '60px',
                            height: '60px',
                            borderRadius: '50%',
                            background: '#ff4d4f',
                            color: 'white',
                            fontSize: '30px',
                            border: 'none',
                            boxShadow: '0 4px 10px rgba(0,0,0,0.3)',
                            cursor: 'pointer',
                            zIndex: 1001,
                            display: 'flex',
                            alignItems: 'center',
                            justifyContent: 'center'
                        }}
                    >
                        Ôºã
                    </button>
                )}
            </div>

            {/* ÊäïÁ®ø„É¢„Éº„ÉÄ„É´ */}
            {isModalOpen && (
                <div
                    style={{
                        position: 'fixed',
                        top: 0,
                        left: 0,
                        right: 0,
                        bottom: 0,
                        background: 'rgba(0,0,0,0.6)',
                        zIndex: 2000,
                        display: 'flex',
                        alignItems: 'center',
                        justifyContent: 'center'
                    }}
                >
                    <div
                        style={{
                            background: 'white',
                            padding: '20px',
                            borderRadius: '10px',
                            width: '90%',
                            maxWidth: '400px',
                            maxHeight: '90vh',
                            overflowY: 'auto' // „Çπ„ÇØ„É≠„Éº„É´ÂØæÂøú
                        }}
                    >
                        <h2 style={{ marginTop: 0, fontSize: '18px' }}>‰∏çÂÆâ„ÇíÊäïÁ®ø„Åô„Çã</h2>

                        {user?.is_anonymous && (
                            <p
                                style={{
                                    fontSize: '12px',
                                    color: '#d32f2f',
                                    background: '#ffebee',
                                    padding: '8px',
                                    borderRadius: '4px',
                                    marginBottom: '15px'
                                }}
                            >
                                ‚Äª„Ç≤„Çπ„ÉàÊäïÁ®ø„É¢„Éº„Éâ„Åß„ÅôÔºà„É≠„Ç∞„Ç§„É≥„ÅßÂ±•Ê≠¥‰øùÂ≠òÂèØÔºâ
                            </p>
                        )}

                        <form onSubmit={handlePost}>
                            {/* 1. ‰∏çÂÆâ„ÅÆÁ®ÆÈ°ûÔºàÂøÖÈ†à„Éª„É©„Ç∏„Ç™„Éú„Çø„É≥È¢®UIÔºâ */}
                            <div style={{ marginBottom: '20px' }}>
                                <label style={{ display: 'block', marginBottom: '8px', fontWeight: 'bold' }}>
                                    „Åì„ÅÆÂ†¥ÊâÄ„Åß„ÄÅ‰Ωï„Åå‰∏ÄÁï™‰∏çÂÆâ„Åß„Åô„ÅãÔºü <span style={{ color: 'red', fontSize: '12px' }}>*ÂøÖÈ†à</span>
                                </label>
                                <div style={{ display: 'flex', flexDirection: 'column', gap: '8px' }}>
                                    {REASONS.map((reason) => (
                                        <label
                                            key={reason}
                                            style={{
                                                display: 'flex',
                                                alignItems: 'center',
                                                padding: '10px',
                                                border: formReason === reason ? '2px solid #ff4d4f' : '1px solid #ccc',
                                                borderRadius: '6px',
                                                cursor: 'pointer',
                                                background: formReason === reason ? '#fff1f0' : 'white'
                                            }}
                                        >
                                            <input
                                                type="radio"
                                                name="reason"
                                                value={reason}
                                                checked={formReason === reason}
                                                onChange={() => setFormReason(reason)}
                                                style={{ marginRight: '10px' }}
                                            />
                                            <span style={{ fontWeight: formReason === reason ? 'bold' : 'normal' }}>{reason}</span>
                                        </label>
                                    ))}
                                </div>
                            </div>

                            {/* 2. Ë©≥Á¥∞„Çø„Ç∞ÈÅ∏ÊäûÔºà‰ªªÊÑè„ÉªË§áÊï∞ÈÅ∏ÊäûÔºâ */}
                            <div style={{ marginBottom: '20px' }}>
                                <label style={{ display: 'block', marginBottom: '8px', fontWeight: 'bold', fontSize: '14px' }}>
                                    Â∑Æ„ÅóÊîØ„Åà„Å™„Åë„Çå„Å∞„ÄÅÂΩì„Å¶„ÅØ„Åæ„Çã„ÇÇ„ÅÆ„ÇíÈÅ∏„Çì„Åß„Åè„Å†„Åï„ÅÑ
                                    <span style={{ fontWeight: 'normal', fontSize: '11px', color: '#666', marginLeft: '5px' }}>ÔºàË§áÊï∞ÂèØÔºâ</span>
                                </label>
                                <div style={{ display: 'flex', flexWrap: 'wrap', gap: '6px' }}>
                                    {REASON_TAGS[formReason].map((tag) => {
                                        const isSelected = formTags.includes(tag);
                                        return (
                                            <button
                                                key={tag}
                                                type="button"
                                                onClick={() => toggleTag(tag)}
                                                style={{
                                                    padding: '6px 10px',
                                                    fontSize: '12px',
                                                    border: isSelected ? '1px solid #0070f3' : '1px solid #ddd',
                                                    borderRadius: '20px',
                                                    background: isSelected ? '#e6f7ff' : '#f9f9f9',
                                                    color: isSelected ? '#0070f3' : '#333',
                                                    cursor: 'pointer'
                                                }}
                                            >
                                                {tag}
                                            </button>
                                        );
                                    })}
                                </div>
                            </div>

                            {/* 3. ÊôÇÈñìÂ∏Ø */}
                            <div style={{ marginBottom: '20px' }}>
                                <label style={{ display: 'block', marginBottom: '8px', fontWeight: 'bold' }}>ÊôÇÈñìÂ∏Ø</label>
                                <div style={{ display: 'flex', gap: '10px' }}>
                                    <label>
                                        <input
                                            type="radio"
                                            name="time"
                                            value="day"
                                            checked={formTime === 'day'}
                                            onChange={(e) => setFormTime(e.target.value)}
                                        />{' '}
                                        Êòº
                                    </label>
                                    <label>
                                        <input
                                            type="radio"
                                            name="time"
                                            value="evening"
                                            checked={formTime === 'evening'}
                                            onChange={(e) => setFormTime(e.target.value)}
                                        />{' '}
                                        Â§ïÊñπ
                                    </label>
                                    <label>
                                        <input
                                            type="radio"
                                            name="time"
                                            value="night"
                                            checked={formTime === 'night'}
                                            onChange={(e) => setFormTime(e.target.value)}
                                        />{' '}
                                        Â§ú
                                    </label>
                                </div>
                            </div>

                            {/* „Éú„Çø„É≥ */}
                            <div style={{ display: 'flex', gap: '10px', marginTop: '20px' }}>
                                <button
                                    type="button"
                                    onClick={() => setIsModalOpen(false)}
                                    style={{ flex: 1, padding: '10px', background: '#ccc', border: 'none', borderRadius: '4px', cursor: 'pointer' }}
                                >
                                    „Ç≠„É£„É≥„Çª„É´
                                </button>
                                <button
                                    type="submit"
                                    disabled={isSubmitting}
                                    style={{
                                        flex: 1,
                                        padding: '10px',
                                        background: '#ff4d4f',
                                        color: 'white',
                                        border: 'none',
                                        borderRadius: '4px',
                                        cursor: 'pointer'
                                    }}
                                >
                                    {isSubmitting ? 'ÈÄÅ‰ø°‰∏≠...' : 'ÊäïÁ®ø„Åô„Çã'}
                                </button>
                            </div>
                        </form>
                    </div>
                </div>
            )}

            <AuthModal isOpen={isAuthModalOpen} onClose={() => setIsAuthModalOpen(false)} />
        </main>
    );
}
